.glass-card {
  position: relative;
  width: 100%;
  max-width: 600px;
  padding: 2.5rem 2rem;
  border-radius: 1.5rem;
  background: linear-gradient(135deg,
      oklch(0.18 0.02 280 / 0.9) 0%,
      oklch(0.14 0.015 280 / 0.95) 50%,
      oklch(0.18 0.02 280 / 0.9) 100%);
  backdrop-filter: blur(24px) saturate(1.3);
  -webkit-backdrop-filter: blur(24px) saturate(1.3);
  border: 1px solid oklch(1 0 0 / 0.12);
  box-shadow: 0 0 0 1px oklch(0 0 0 / 0.3),
    0 4px 16px oklch(0 0 0 / 0.25),
    0 16px 48px oklch(0 0 0 / 0.2),
    inset 0 1px 0 oklch(1 0 0 / 0.08);
}
.glass-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg,
      transparent 0%,
      oklch(1 0 0 / 0.2) 20%,
      oklch(1 0 0 / 0.3) 50%,
      oklch(1 0 0 / 0.2) 80%,
      transparent 100%);
  border-radius: 1.5rem 1.5rem 0 0;
}
