  .glass-card {
    padding: 3rem 3.5rem;
  }
}
@media (min-width: 768px) {
  .glass-card {
    padding: 3.5rem 4rem;
    max-width: 680px;
  }
}
.nav-link {
  display: inline-block;
  padding: 0.5rem 0;
  font-family: var(--font-mono);
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  color: oklch(0.85 0 0 / 0.7);
  background: transparent;
  border: none;
  position: relative;
  cursor: pointer;
  transition: color 0.25s ease-out, text-shadow 0.25s ease-out;
}
.nav-link::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0.25rem;
  width: 0;
  height: 1px;
  background: var(--accent);
  transition: width 0.25s ease-out;
}
.nav-link:hover {
  color: var(--accent);
  text-shadow: 0 0 20px oklch(0.78 0.28 295 / 0.4);
}
.nav-link:hover::after {
  width: 100%;
}
@media (min-width: 640px) {
  .nav-link {
    font-size: 0.75rem;
  }
}
